<template><section><h1>Component B</h1>
<p>The description for Component B goes here.</p>
<h3>Example</h3>
<component-demo ref="component-demo"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">component-b</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"onClick"</span>/&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">import</span> { ComponentB } <span class="hljs-keyword">from</span> <span class="hljs-string">'mylib'</span>;

<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    onClick(value) {
      alert(<span class="hljs-string">"Component B has value: "</span> + value);
    }
  },
  <span class="hljs-attr">components</span>: {
    ComponentB
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- component-demo.vue --&gt;</span>
</code></pre>
<h3>Events</h3>
<table>
<thead>
<tr>
<th>Name</th>
<th>Params</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>click</code></td>
<td>value</td>
<td>Fire after the button is clicked, with value in text field</td>
</tr>
</tbody>
</table>
</section></template><script>
import { ComponentB } from 'mylib';


export default {components:{'component-demo':{"template":"\n  <component-b @click=\"onClick\"/>\n",
  methods: {
    onClick(value) {
      alert("Component B has value: " + value);
    }
  },
  components: {
    ComponentB
  }
}}}</script>


